@font-face {
      font-family: 'sans';
      src: url(./assets/font/Sans.ttf);
  }

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'sans';
    list-style: none;
}
a{
  text-decoration: none;
  color: inherit;
}
img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }
  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }
  body{
    // overflow-x: hidden;
  }